body 
    background: #FFF
    font-family: 'Literal'
    font-style: normal

.container
    max-width: 1800px 
    margin: 0 auto
    padding: 0 20px
    box-sizing: content-box

.btn__yellow
    max-width: 220px
    width: 100%
    display: block
    padding: 16px
    color: #FDC05A
    text-align: center
    font-size: 20px
    font-weight: 700
    line-height: normal
    border-radius: 30px
    border: 2px solid #FDC05A

.nav
    width: calc( 100vw - 40px )
    max-width: 1800px
    padding: 15px 23px 
    display: flex
    justify-content: flex-start
    align-items: center
    border-radius: 32px
    background: rgba(255, 255, 255, 0.86)
    box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.04)
    backdrop-filter: blur(7px)
    position: fixed
    top: 20px
    left: 50%
    transform: translateX(-50%)
    z-index: 5
    &__logo
        max-width: 128px
        img
            width: 100%
    &__list
        display: flex
        justify-content: center
        align-items: center
        gap: 35px
        margin: 0 auto
        li
            position: relative
        a
            color: #B84E12
            font-size: 18px
            font-weight: 400
            line-height: normal
            letter-spacing: 0.36px
    &__drop
        display: grid
        grid-template-columns: 1fr 16px
        gap: 10px
        align-items: center
        &::after
            content: ''
            display: block
            width: 16px
            height: 16px
            background: url('../img/drop.svg') center no-repeat
            background-size: contain
        &_list
            display: none
        &.active
            &::after
                background: url('../img/nav_drop_active.svg') center no-repeat
            &+.nav__drop_list
                display: grid
                grid-template-columns: 100%
                gap: 10px
                position: absolute
                top: 30px
                right: -6px
                width: 280px
                padding: 20px 34px 30px
                border-radius: 32px 0 32px 32px
                background: rgba(250, 236, 221, 0.98)
                a
                    display: grid
                    grid-template-columns: 40px 1fr
                    gap: 21px
                    align-items: center
                    &:hover
                        img
                            opacity: 1
                p
                    color: #2B0F08
                    font-size: 20px
                    font-weight: 400
                    line-height: 100%
                img
                    width: 100%
                    opacity: .45
                    transition: all .3s
                &::before
                    content: ''
                    display: block
                    position: absolute
                    top: -44px
                    right: 0
                    background: rgba(250, 236, 221, 0.98)
                    border-radius: 32px 32px 0 0
                    width: 27px
                    height: 44px
                    z-index: -1
    &__search
        position: relative
        max-width: 240px
        input
            display: block
            box-sizing: border-box  
            padding: 5px 24px
            // border-image: linear-gradient(to right, #FDC05A, #B84E12) 1
            // border-width: 2px
            // border-style: solid
            border: solid 2px #B84E12
            border-radius: 59px
            color: #B84E12
            font-size: 16px
            font-weight: 400
            line-height: normal
            letter-spacing: 0.32px
            &::placeholder
                color: #FDC05A
                opacity: 0.65
        a
            position: absolute
            right: 7px
            top: 50%
            transform: translateY(-50%)
    &__profile
        margin: 0 60px 0 25px
        display: grid
        grid-template-columns: 33px 1fr
        gap: 6px
        align-items: center
        img
            width: 100%
        p
            color: #2B0F08
            font-size: 18px
            font-weight: 400
            line-height: normal
            letter-spacing: 0.36px
    &__cart
        display: grid
        grid-template-columns: 1fr 27px
        gap: 8px
        align-items: center
        img
            width: 100%
        p
            color: #2B0F08
            font-size: 18px
            font-weight: 400
            line-height: normal
            letter-spacing: 0.36px

.header
    background: url('../img/header_bg_2.jpg') lightgray 50% / cover no-repeat
    padding: 115px 0 60px
    position: relative
    overflow: hidden
    z-index: 1
    &__wrapper
        padding: 142px 194px 152px
        background: url('../img/header_bg.jpg') lightgray 50% / cover no-repeat
        border-radius: 430px
    h1
        color: #FFF
        font-size: 86px
        font-weight: 700
        line-height: normal
        text-transform: uppercase
        display: grid
        grid-template-columns: 74px 1fr
        gap: 20px
        align-items: center
        &::before
            content: ''
            display: block
            width: 74px
            height: 74px
            background: url('../img/gif.gif') center no-repeat
            background-size: contain
            filter: grayscale(1) brightness(1000%)
    p
        color: #FFF
        font-size: 26px
        font-weight: 400
        line-height: 138%
        max-width: 485px
        margin: 15px 0 0
    .btn__yellow
        margin: 56px 0 0
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.12)
    &::before
        content: ''
        display: block
        position: absolute
        inset: 0
        pointer-events: none
        background: url('../img/header_filter.png') center no-repeat
        mix-blend-mode: soft-light
        background-size: cover

.puzzle
    padding: 180px 0 25px
    background: #FAECDD
    position: relative
    z-index: 1
    &::before
        content: ''
        display: block
        position: absolute
        inset: 0
        pointer-events: none
        background: url('../img/puzzle_filter.png') center repeat
        mix-blend-mode: soft-light
        background-size: cover
        z-index: -1
    &__title
        width: 100%
        max-width: 860px
        position: absolute
        top: 60px
        left: 50%
        transform: translateX(-50%)
        z-index: -2
        img
            width: 100%
    &__wrapper
        display: flex
        justify-content: center
        align-items: center
        img
            width: 100%
            max-width: 631px
        p
            position: absolute
            color: #B84E12
            font-size: 26px
            font-weight: 700
            line-height: 98%
            letter-spacing: 0.26px
            z-index: 1
            &:nth-child(1)
                left: 12%
                top: 409px
            &:nth-child(2)
                left: 19%
                top: 555px
            &:nth-child(3)
                left: 10%
                top: 726px
            &:nth-child(4)
                right: 12%
                top: 409px
                text-align: right
            &:nth-child(5)
                right: 19%
                top: 555px
                text-align: right
            &:nth-child(6)
                right: 10%
                top: 726px
                text-align: right

.collection
    padding: 100px 0 90px
    h2
        color: #B84E12
        text-align: center
        font-size: 66px
        font-weight: 700
        line-height: 100%
    &__subtitle
        margin: 18px auto 0
        max-width: 960px
        color: #2B0F08
        text-align: center
        font-size: 26px
        font-weight: 400
        line-height: 112%
        letter-spacing: -0.26px
    &__wrapper
        margin: 60px auto 0
        display: grid
        grid-template-columns: 1fr 1fr 1fr 1fr
        gap: 35px
        max-width: 1160px
    &__item
        // display: grid
        // grid-template-columns: 100%
        // grid-template-rows: 1fr 54px
        img
            width: 100%
            height: calc( 100% - 54px )
            object-fit: contain
            object-position: center bottom
        p
            color: #B84E12
            text-align: center
            font-size: 26px
            font-weight: 700
            line-height: 102%
            // height: 54px

.slider
    position: relative
    overflow: hidden
    &__black
        height: 100%
        background: #2B0F08
        position: relative
        overflow: hidden
        &::before
            content: ''
            display: block
            position: absolute
            inset: 0
            pointer-events: none
            background: url('../img/puzzle_filter.png') center repeat
            mix-blend-mode: soft-light
            background-size: cover
            z-index: 2
    .swiper
        &-slide
            display: grid
            grid-template-columns: 1fr 1fr
    &__img
        z-index: 2
        img
            width: 100%
            height: 100%
            object-fit: cover
    &__info
        padding: 180px 60px
        max-width: 634px
        margin: 0 130px 0 auto
        width: 100%
        img
            width: 86px
            margin: 30px 0 0
    &__title
        color: #FDC05A
        font-size: 36px
        font-weight: 700
        line-height: 110.5%
    &__text
        margin: 30px 0 0
        color: #E0800E
        font-size: 26px
        font-weight: 700
        line-height: 110.5%
    &__prev
        bottom: 160px
        left: calc( 50% - 704px)
        margin: 0
        top: auto
        width: 163px
        height: auto
        svg
            width: 100%
        &::after
            content: none
    &__next
        bottom: 160px
        left: calc( 50% - 501px)
        margin: 0
        top: auto
        width: 163px
        height: auto
        svg
            width: 100%
            transform: rotate(180deg)
        &::after
            content: none
    &__red
        background: #B84E12
        position: relative
        overflow: hidden
        height: 100%
        &::before
            content: ''
            display: block
            position: absolute
            inset: 0
            pointer-events: none
            background: url('../img/puzzle_filter.png') center repeat
            mix-blend-mode: soft-light
            background-size: cover
            z-index: 2

.avatar
    padding: 266px 0 205px
    background: url('../img/avatar_desc.jpg') center no-repeat
    background-size: cover
    &__info
        margin: 0 0 0 calc(50% - 704px)
        max-width: 561px
    &__new
        color: #E0800E
        font-size: 20px
        font-weight: 700
        line-height: normal
        letter-spacing: 0.6px
        margin: 0 0 -15px
    h2
        color: #FDC05A
        font-size: 86px
        font-weight: 700
        line-height: normal
        text-transform: uppercase
    &__text
        margin: 20px 0 0
        color: #FFF
        font-size: 26px
        font-weight: 700
        line-height: 110.5%
    .btn__yellow
        max-width: 240px
        margin: 105px 0 0

.video
    padding: 78px 0
    background: url('../img/video_bg.jpg') center no-repeat
    background-blend-mode: multiply, lighten, normal
    background-size: cover
    &__more
        display: none
    &__wrapper
        border-radius: 430px
        background: url('../img/video_prev.jpg') center no-repeat
        box-shadow: 2px 4px 42px 0px rgba(0, 0, 0, 0.12)
        min-height: 684px
        display: flex
        flex-direction: column
        align-items: center
        justify-content: flex-end
        padding: 40px
        position: relative
    &__play
        width: 152px
        height: 152px
        border-radius: 100%
        background: rgba(255, 255, 255, 0.12)
        opacity: 0.9
        backdrop-filter: blur(3px)
        display: flex
        justify-content: center
        align-items: center
        position: absolute
        top: 50%
        left: 50%
        transform: translate(-50%, -50%)
        img
            width: 43%
            object-position: right center
            object-fit: contain
            height: 29%
    h2
        color: #FDC05A
        text-align: center
        font-size: 66px
        font-weight: 700
        line-height: 98%
        letter-spacing: 0.66px
    p
        margin: 16px 0 0
        color: #FFF
        text-align: center
        font-size: 26px
        font-weight: 400
        line-height: 132%
        letter-spacing: -0.26px

.news
    padding: 60px 0 80px
    h2
        color: #B84E12
        font-size: 66px
        font-weight: 700
        line-height: normal
        display: grid
        grid-template-columns: 54px auto
        gap: 20px
        align-items: center
        justify-content: center
        &::before
            content: ''
            display: block
            width: 54px
            height: 54px
            background: url('../img/news_logo.svg') center no-repeat
            background-size: contain
    &__wrapper
        margin: 50px auto 0
        display: grid
        grid-template-columns: 79fr 61fr
        max-width: 1410px
        gap: 10px
    &__show
        grid-column: 1/2
        grid-row: 1/5
        border: 1px solid #C83709
        .news
            &__img
                img
                    width: 100%
                    height: 420px
                    object-fit: cover
            &__row
                margin: 24px 27px 0
            &__name
                color: #C83709
                font-size: 36px
                font-weight: 700
                line-height: 105%
            &__date
                color: #C83709
                font-size: 24px
                font-weight: 700
                line-height: normal
            &__text
                color: #2B0F08
                font-size: 18px
                font-weight: 400
                line-height: 124%
                letter-spacing: -0.18px
                margin: 14px 27px 34px
                max-width: 704px
    &__row
        display: flex
        justify-content: space-between
        align-items: center
    &__item
        border: 1px solid #C83709
        display: grid
        grid-template-columns: 140px 1fr
        .news
            &__img
                grid-row: 1/3
                img
                    width: 100%
                    height: 100%
                    object-fit: cover
            &__row
                margin: 22px 18px 0 22px 
            &__name
                color: #C83709
                font-size: 20px
                font-weight: 700
                line-height: 105%
            &__date
                color: #C83709
                font-size: 18px
                font-weight: 400px
                line-height: normal
            &__text
                color: #2B0F08
                font-size: 18px
                font-weight: 400
                line-height: 124%
                letter-spacing: -0.18px
                margin: 12px 18px 30px 22px
                max-width: 704px
                max-width: 380px
    .btn__yellow
        border: 1px solid #B84E12
        max-width: 320px
        color: #B84E12
        margin: 60px auto 0

.comment
    padding: 55px 0 70px
    background: url('../img/comment_bg.jpg') center no-repeat
    background-size: cover
    &__info,
    &__ask
        display: none
    h2
        color: #B84E12
        text-align: center
        font-size: 66px
        font-weight: 700
        line-height: normal
    &__wrapper
        margin: 42px auto 0
        max-width: 931px
        display: grid
        gap: 26px
        grid-template-columns: 100%
    &__item
        display: grid
        grid-template-columns: 190px 1fr
        gap: 45px
        align-content: start
    &__name
        display: grid
        grid-template-columns: 50px 1fr
        gap: 17px
        align-items: center
        img
            width: 100%
            border-radius: 100%
            height: 50px
        p
            color: #B84E12
            font-size: 16px
            font-weight: 700
            line-height: normal
    &__rate
        margin: 17px 0 0
        display: flex
        justify-content: space-between
        align-items: center
    &__star
        display: grid
        grid-template-columns: 20px 20px 20px 20px 20px
        gap: 3px
        img
            width: 100%
    &__date
        color: #2B0F08
        font-size: 16px
        font-weight: 400
        line-height: normal
    &__text
        color: #2B0F08
        font-size: 18px
        font-weight: 400
        line-height: normal
        a
            color: #B84E12
            font-weight: 700
    &__link
        margin: 60px 0 0
        display: grid
        grid-template-columns: 260px 260px
        gap: 24px
        justify-content: center
        .btn__yellow
            max-width: 100%
            border: 1px solid #B84E12
            color: #B84E12
    &__btn
        border-radius: 30px
        background: #FDC05A
        display: block
        width: 100%
        color: #B84E12
        font-size: 20px
        font-weight: 700
        line-height: normal
        padding: 12px 15px

.about-block
    background: url('../img/about-block_bg.jpg') center no-repeat
    background-size: cover
    position: relative
    padding: 150px 0 173px
    &__img
        position: absolute
        left: 0
        bottom: -200px
        pointer-events: none
        max-width: 950px
        img
            width: 100%
    &__info
        max-width: 940px
        margin: 0 calc(50% - 704px) 0 auto
        display: grid
        grid-template-columns: 100%
        gap: 12px
        h2
            color: #FDC05A
            font-size: 66px
            font-weight: 700
            line-height: normal
        p
            color: #FFF
            font-size: 18px
            font-weight: 400
            line-height: normal

.acc-mob
    display: none
    
.footer 
    background: #2B0F08
    padding: 60px 0 50px
    &__wrapper
        display: grid
        grid-template-columns: 450px 1fr
        gap: 170px
        align-items: end
        position: relative
    &__list
        display: grid
        grid-template-columns: auto auto auto
        a
            color: #FFF
            font-size: 18px
            font-weight: 400
            line-height: 210%
    &__main
        display: grid
        grid-template-columns: 1fr auto
        column-gap: 20px
    &__email
        grid-row: 1/4
        grid-column: 2/3
        height: 100%
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: space-between
        a
            color: #FDC05A
            font-size: 20px
            font-weight: 700
            line-height: 120%
        p
            color: #FFF
            font-size: 20px
            font-weight: 400
            line-height: 120%
    &__logo
        max-width: 126px
        img
            width: 100%
    &__phone
        color: #FFF
        font-size: 20px
        font-weight: 400
        line-height: normal
        margin: 30px 0 0
    &__soc
        grid-template-columns: 2fr 3fr 2fr 2fr
        align-items: center
        gap: 12px
        display: grid
        margin: 10px 0 0
        max-width: 175px
        img
            width: 100%
            object-fit: cover
    &__arrow
        position: absolute
        right: 0
        top: 50%
        transform: translateY(-50%) 
        cursor: pointer

@media ( max-width: 1600px )
    .nav
        &__list
            gap: 20px
            a
                font-size: 16px
        &__search
            input
                font-size: 12px
        &__profile
            margin: 0 25px
            p
                font-size: 12px
        &__cart
            p
                font-size: 12px
    .puzzle
        &__wrapper
            position: relative
            p
                &:nth-child(1)
                    left: 0
                    top: 28%
                &:nth-child(2)
                    left: 100px
                    top: 47%
                &:nth-child(3)
                    left: 0
                    top: 68%
                &:nth-child(4)
                    right: 0
                    top: 28%
                &:nth-child(5)
                    right: 100px
                    top: 47%
                &:nth-child(6)
                    right: 0
                    top: 68%
    .slider
        &__info
            padding: 180px 20px
            margin: 0 
        &__next
            bottom: 20px
            left: 203px
        &__prev
            bottom: 20px
            left: 20px
    .avatar
        &__info
            margin: 0
    .about-block
        &__img
            width: 500px
            bottom: -120px
        &__info
            margin: 0 0 0 auto
            max-width: calc( 100% - 500px )
    .footer
        &__wrapper
            gap: 50px
            grid-template-columns: 380px 1fr
        &__list
            a
                font-size: 14px
        &__phone
            font-size: 16px
        &__soc
            max-width: 140px
        &__email
            a,
            p
                font-size: 16px

@media ( max-width: 1300px )
    .nav
        &__list
            gap: 20px
            a
                font-size: 14px

@media ( max-width: 1200px )
    .nav
        &__search
            width: auto
            input
                display: none
            a
                position: relative
                display: block
                right: auto
                transform: none
        &__profile
            grid-template-columns: 26 1fr
            img
                width: 26px
        &__cart
            grid-template-columns: 1fr 26
            img
                width: 26px
    .header
        &__wrapper
            padding: 140px 100px 150px
    .video
        h2
            font-size: 50px
        p
            font-size: 20px

@media ( max-width: 1024px )
    .nav
        &__list
            display: none
        &__profile
            margin: 0 12px 0 auto
            p
                display: none
            img
                width: 33px
        &__cart
            display: none
        &__search
            display: none
        &__humburger
            display: flex
            position: relative
            height: 20px
            width: 26px
            cursor: pointer
            &::before,
            &::after,
            span
                content: ''
                display: block
                position: absolute
                width: 100%
                height: 4px
                border-radius: 2px
                background: #B84E12
                // transition: all .3s
                left: 0
            &::before
                bottom: 0
            &::after
                top: 0
            span
                top: 50%
                transform: translateY(-50%)
        &.active
            top: 0
            left: 0
            border-radius: 0
            transform: none
            width: 100%
            flex-wrap: wrap
            height: 100vh
            overflow-x: scroll
            align-content: start
            .nav
                &__humburger
                    span
                        opacity: 0
                    &::before,
                    &::after
                        left: 50%
                        top: 50%
                        transform: translate(-50%, -50%) rotate(45deg)
                    &::after
                        transform: translate(-50%, -50%) rotate(-45deg)
                &__list
                    width: 100%
                    order: 2
                    display: grid
                    grid-template-columns: 100%
                    gap: 10px
                    margin: 20px 0 0
                    align-items: center
                    justify-items: center
                    a
                        text-align: center
                        font-size: 16px
                &__drop
                    display: block
                    &::after
                        content: none
                    &_list
                        display: none
                &__search
                    display: block
                    width: calc( 50% - 10px )
                    order: 3
                    max-width: 100%
                    margin: 20px auto 0 0
                    input
                        display: block
                        width: 100%
                    a
                        position: absolute
                        right: 6px
                        transform: translateY(-50%)
                &__cart
                    display: grid
                    width: calc( 50% - 10px )
                    margin: 20px 0 0
                    order: 4
                    max-width: 100%
                    p
                        text-align: right
    .header
        padding: 160px 0 100px
        background: linear-gradient(0deg, rgba(0, 0, 0, 0.35) 0%, rgba(0, 0, 0, 0.35) 100%), url('../img/header_bg_2.jpg')
        background-position: bottom right
        &__wrapper
            border-radius: 0
            padding: 0
            background: transparent
        h1
            grid-template-columns: 74px auto
            justify-content: center
        p
            margin: 20px auto 0
            text-align: center
        .btn__yellow
            margin: 46px auto 0
    .puzzle
        padding: 336px 0 50px
        &__title
            position: relative
            max-width: calc( 100% - 60px )
            margin: 0 auto
            transform: none
            left: auto
            top: auto
            z-index: 1
        &__wrapper
            position: static
            display: grid
            grid-template-columns: 1fr 1fr
            gap: 20px
            row-gap: 36px
            margin: 40px 0 0
            p
                position: relative
                left: auto !important
                right: auto !important
                top: auto !important
                max-width: 100%
                text-align: left !important
            img
                position: absolute
                top: -50px
                left: 50%
                transform: translateX(-50%)
                max-width: 302px
                z-index: 2
    .collection
        &__wrapper
            grid-template-columns: 1fr 1fr 1fr
    .slider
        .swiper
            &-slide
                grid-template-columns: 100%
        &__info
            padding: 40px 20px
